<template>
	<div>
		<el-breadcrumb separator="/">
			<el-breadcrumb-item>系統管理</el-breadcrumb-item>
			<el-breadcrumb-item>发布</el-breadcrumb-item>
		</el-breadcrumb>
		<el-card>
			<el-row>
				<el-col :span="5">
					<el-button type="primary" @click="dialogVisible=true">
						发布
					</el-button>
				</el-col>
			</el-row>
			<el-divider></el-divider>
			<el-table :data="datalist" border stripe style="width: 100%" max-height="1000px">
				<el-table-column  fixed="left" prop="event" label="活动名称" width="100"> </el-table-column>
				<el-table-column prop="region" label="活动地点" width="200"> </el-table-column>
				<el-table-column prop="date1" label="开始日期" width="150"> </el-table-column>
				<el-table-column prop="date2" label="结束日期" width="150"> </el-table-column>
				<el-table-column prop="num" label="已招人数" width="150"> </el-table-column>
				<el-table-column prop="amount" label="所需人数" width="150"> </el-table-column>
				<el-table-column prop="duration" label="时长" width="150"> </el-table-column>
				<el-table-column prop="linkman" label="联系人" width="150"> </el-table-column>
				<el-table-column prop="phone" label="联系人电话" width="200"> </el-table-column>
				<el-table-column label="操作" width="300">
					<template slot-scope="scope">
						<el-button type="primary" icon="el-icon-edit" @click="editdialogVisible=true">编辑</el-button>
						<el-button type="danger" icon="el-icon-delete" @click="removeitem">删除</el-button>
					</template>
				</el-table-column>
			</el-table>

		</el-card>

		<el-dialog title="发布表单" :visible.sync="dialogVisible" width="40%">
			<el-form :model="addForm" :rules="rules" ref="ruleForm" label-width="100px">
				<el-form-item label="活动名称" prop="event">
					<el-col :span="16">
						<el-input v-model="addForm.event"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="活动地点" prop="region">
					<el-col :span="16">
						<el-input v-model="addForm.region"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="活动时间" required>
					<el-col :span="11">
						<el-form-item prop="date1">
							<el-date-picker type="date" placeholder="开始日期" v-model="addForm.date1" style="width: 100%;"></el-date-picker>
						</el-form-item>
					</el-col>
					<el-col class="line" :span="2">-</el-col>
					<el-col :span="11">
						<el-form-item prop="date2">
							<el-date-picker type="date" placeholder="结束日期" v-model="addForm.date2" style="width: 100%;"></el-date-picker>
						</el-form-item>
					</el-col>
				</el-form-item>
				<el-form-item label="人数" prop="amount">
					<el-col :span="16">
						<el-input v-model.numer="addForm.amount" autocomplete="off"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="累计时长" prop="duration">
					<el-col :span="16">
						<el-input v-model.number="addForm.duration"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="联系人" prop="linkman">
					<el-col :span="16">
						<el-input v-model.numer="addForm.linkman"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="联系电话" prop="phone">
					<el-col :span="16">
						<el-input v-model.numer="addForm.phone"></el-input>
					</el-col>
				</el-form-item>

				<el-form-item label="备注">
					<el-col :span="16">
						<el-input type="textarea" v-model="addForm.desc"></el-input>
					</el-col>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogclose('ruleForm')">重置</el-button>
				<el-button type="primary" @click="submitForm('ruleForm')">发布</el-button>
			</span>
		</el-dialog>


		<el-dialog title="编辑" :visible.sync="editdialogVisible" width="40%">
			<el-form :model="editForm" :rules="rules" ref="editForm" label-width="100px">
				<el-form-item label="活动名称" prop="event">
					<el-col :span="16">
						<el-input v-model="editForm.event"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="活动地点" prop="region">
					<el-col :span="16">
						<el-input v-model="editForm.region"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="活动时间" required>
					<el-col :span="11">
						<el-form-item prop="date1">
							<el-date-picker type="date" placeholder="开始日期" v-model="editForm.date1" style="width: 100%;"></el-date-picker>
						</el-form-item>
					</el-col>
					<el-col class="line" :span="2">-</el-col>
					<el-col :span="11">
						<el-form-item prop="date2">
							<el-date-picker type="date" placeholder="结束日期" v-model="editForm.date2" style="width: 100%;"></el-date-picker>
						</el-form-item>
					</el-col>
				</el-form-item>
				<el-form-item label="已招人数">
					<el-col :span="16">
						<el-input disabled v-model.numer="editForm.num" autocomplete="off"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="人数" prop="amount">
					<el-col :span="16">
						<el-input v-model.numer="editForm.amount" autocomplete="off"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="累计时长" prop="duration">
					<el-col :span="16">
						<el-input v-model.number="editForm.duration"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="联系人" prop="linkman">
					<el-col :span="16">
						<el-input v-model.numer="editForm.linkman"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="联系电话" prop="phone">
					<el-col :span="16">
						<el-input v-model.numer="editForm.phone"></el-input>
					</el-col>
				</el-form-item>

				<el-form-item label="备注">
					<el-col :span="16">
						<el-input type="textarea" v-model="editForm.desc"></el-input>
					</el-col>
				</el-form-item>
			</el-form>
			<span slot="footer" class="dialog-footer">
				<el-button @click="editdialogVisible=false">取消</el-button>
				<el-button type="primary" @click="submitForm('editForm')">修改</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import eventbus from '../../public/eventbus.js'
	export default {
		name: 'release',
		data() {
			var notzero = (rule, value, cb) => {
				if (value <= 0) {
					cb(new Error('输入正数'))
				} else {
					cb()
				}

			};
			var checkPhone = (rule, value, callback) => {
				if (!value) {
					return callback(new Error('手机号不能为空'));
				} else {
					const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
					console.log(reg.test(value));
					if (reg.test(value)) {
						callback();
					} else {
						return callback(new Error('请输入正确的手机号'));
					}
				}
			};
			return {
				datalist: [{
					event: '扫地',
					region: '校医院',
					date1: '2020-1-1',
					date2: '2020-1-1',
					num: 20,
					amount: 30,
					duration: 20,
					desc: '',
					linkman: '张三',
					phone: '12312312'

				}],
				dialogVisible: false,
				editdialogVisible: false,
				addForm: {
					event: '',
					region: '',
					date1: '',
					date2: '',
					amount: '',
					duration: '',
					desc: '',
					linkman: '',
					phone: ''

				},
				rules: {
					event: [{
						required: true,
						message: '请输入活动名称',
						trigger: 'blur'
					}],
					region: [{
						required: true,
						message: '请选择活动区域',
						trigger: 'blur'
					}],
					date1: [{
						required: true,
						message: '请选择日期',
						trigger: 'blur'
					}],
					date2: [{

							required: true,
							message: '请选择日期',
							trigger: 'blur'
						}

					],
					amount: [{
							required: true,
							message: '请输入人数',
							trigger: 'blur'
						},
						{
							type: 'number',
							message: '人数必须为数字值',
							transform: (value) => Number(value)
						},
						{
							validator: notzero,
							trigger: 'blur'
						}

					],
					duration: [{
							required: true,
							message: '请输入时长',
							trigger: 'blur'
						},
						{
							type: 'number',
							message: '时长必须为数字值',
							transform: (value) => Number(value)
						},
						{
							validator: notzero,
							trigger: 'blur'
						}
					],
					linkman: [{
						required: true,
						message: '请输入联系人',
						trigger: 'blur'
					}],
					phone: [{
							required: true,
							message: '请输入联系电话',
							trigger: 'blur'
						},
						{
							validator: checkPhone,
							trigger: 'blur'
						}
					]

				},

				editForm: {
					event: '扫地',
					region: '校医院',
					date1: '2020-1-1',
					date2: '2020-1-1',
					num: 20,
					amount: 30,
					duration: 20,
					desc: '',
					linkman: '张三',
					phone: '12312312'
				}
			}
		},
		/*created() {
			this.getdatalist()
		*/
		methods: {
			async getdatalist() {
				const {data:res}=await this.$http.post("department/query/released",{},{headers: {'Content-Type': 'application/x-www-form-urlencoded'}});

			},

			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						alert('submit!');
						this.dialogVisible = false
					} else {
						console.log('error submit!!');
						return false;
					}

				});
			},
			dialogclose(formName) {
				this.$refs[formName].resetFields();
			},
			async removeitem() {
				const result = await this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning',
				}).catch(err => err)
				if (result !== 'confirm') {
					return this.$message.info('已取消删除')
				}
				else
				{
					const {data:res}=await this.$http.post("",data,{headers: {'Content-Type': 'application/x-www-form-urlencoded'}});
					
				}
			}



		}
	}
</script>

<style scoped="scoped">
	.el-breadcrumb {
		margin-bottom: 15px;
	}
</style>
